<template>
  <div class="textOverflow" ref="textBoxWrap" :style="{width}" @mouseenter="mouseEnter" @mouseleave="mouseLeave">
    <span id="textBox">
      <slot />
    </span>
    <el-tooltip class="item" ref="tooltip" effect="dark" :content="tooltipContent" placement="top" />
  </div>
</template>

<script>
function debounce(fn, time) {
  let timeout = null
  return function (e) {
    clearTimeout(timeout, 200)
    timeout = setTimeout(() => {
      fn.call(this, e)
    }, time)
  }
}
export default {
  name: 'textOverflow',
  props: {
    width: {
      type: String
    }
  },
  data() {
    return {
      tooltip: true,
      tooltipContent: '',
    }
  },
  created() {
  },
  methods: {
    activateTooltip: debounce(tooltip => tooltip.handleShowPopper(), 50),
    mouseEnter() {
      const dom = this.$refs.textBoxWrap
      const range = document.createRange()
      range.setStart(dom, 0)
      range.setEnd(dom, dom.childNodes.length)
      const rangeWidth = range.getBoundingClientRect().width
      const domWidth = dom.getBoundingClientRect().width
      if (domWidth < rangeWidth + 10) {
        this.tooltip = true
        this.tooltipContent = dom.innerText || dom.textContent
        console.log(this.tooltipContent)
        window.dom = dom
        const tooltip = this.$refs.tooltip
        tooltip.referenceElm = dom
        tooltip.$refs.popper && (tooltip.$refs.popper.style.display = 'none')
        tooltip.doDestroy()
        tooltip.setExpectedState(true)
        this.activateTooltip(tooltip)
      } else {
        this.tooltip = false
      }
      console.log(domWidth, rangeWidth)
    },
    mouseLeave() {
      const tooltip = this.$refs.tooltip;
      if (tooltip) {
        tooltip.setExpectedState(false)
        tooltip.handleClosePopper()
      }
    }
  }
}
</script>

<style scoped lang="less">
.textOverflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.textOverflow--3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
</style>
